<template>
  <user>
    <html-head 
      title="Add Product"
    ></html-head>

    <page-header>
      <template v-slot:title >
        <span v-text="`Product Stock`"></span>
      </template>
    </page-header>

    <v-row>
      <v-col cols="12" lg="6">
        <validation-observer ref="validation">
        <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="product.isFetching">
          <v-card elevation="0">
            <v-card-title>Product Informations</v-card-title>
            <v-card-text>
              <form-errors :errors="product.errors"></form-errors>
              <div class="d-flex justify-center">
                <image-selector class="mb-5" :src="product.data.image" @input="product.prestine( false )"></image-selector>
              </div>
              <validation-provider vid="code" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Code"
                  name="code"
                  outlined
                  v-model="product.data.cpde"
                  @input="product.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="name" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Name"
                  name="name"
                  outlined
                  v-model="product.data.name"
                  @input="product.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="description" v-slot="{ errors }">
                <v-textarea
                  :error-messages="errors"
                  label="Description"
                  name="description"
                  v-model="product.data.description"
                  outlined
                ></v-textarea>
              </validation-provider>
              <validation-provider vid="price" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Price"
                  name="price"
                  type="number"
                  step="0.01"
                  outlined
                  v-model="product.data.price"
                  @input="product.prestine( false )"
                ></v-text-field>
              </validation-provider>
              <validation-provider vid="categories" v-slot="{ errors }">
                <category-selector 
                  :errors="errors"
                  v-model="product.data.categories" 
                  :requestOptions="categoryRequestOptions"
                  @input="product.prestine( false )"
                ></category-selector>
              </validation-provider>
            </v-card-text>
            <v-card-actions> 
              <v-btn color="primary" type="submit" :loading="product.isFetching" :disabled="product.isPrestine">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
        </validation-observer>
      </v-col>
      <v-col cols="12" lg="6">
        <variation-list v-model="product.data.variations" @updated="product.getResource( api.alone( $route.params.id ) )"></variation-list>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import Product from './models/Product'
import { mapActions, mapGetters } from 'vuex'

export default {
  components: {
    VariationList: () => import( './cards/VariationList' )
  },
  
  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),

    categoryRequestOptions () {
      return {
        url: Api.category.getGroupByParent(),
        params: {
          q: {
            type: 'product'
          }
        }
      }
    }
  },

  data: () => ({
    api: Api,
    product: new Product,
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),

    async submit () {
      const data = await this.product.request({
        method: 'patch',
        url: Api.alone( this.$route.params.id ),
        data: this.product.readyData( this.$refs.form.$el ),
      })
      if( data ) {
        this.setSnackbar({
          model: true,
          text: 'Product updated',
          timeout: 3000
        })
      }
    },
  },

  mounted () {
    this.product.getResource( Api.alone( this.$route.params.id ) )
    this.product.load( false )
  }
}
</script>